import onemessage from './oneMessage';
import {mapState} from 'vuex';

var userInfo;
var frindInfo;

var socketTask;
var socketOpen = false;

export default {
	components: {
		onemessage,
	},
	data() {
		return {
			/**
			 * ChatMessageCard example
			ChatMessageCard = {
				this.senderId = senderId;
				this.receiverId = receiverId;
				this.msg = msg;		// 显示
				this.msgId = msgId; // 前端不需要用到
				this.creatTime = creatTime; // 显示 flag
			},
			*/
			
			chatContent: [{
				msgId: '0001',
				flag: '1',
				msg: '第一条消息',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0002',
				flag: '1',
				msg: 'abab',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0003',
				flag: '1',
				msg: 'abab',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0004',
				flag: '2',
				msg: 'abab',
				messageTime: '11:29',
				messageStatus: '1'
			}, {
				msgId: '0006',
				flag: '1',
				msg: 'abab',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0006',
				flag: '2',
				msg: 'abab',
				messageTime: '11:29',
				messageStatus: '1'
			}, {
				msgId: '0001',
				flag: '1',
				msg: 'abab',
				messageTime: '11:29',
				messageStatus: '0'
			}, {
				msgId: '0002',
				flag: '1',
				msg: 'abab',
				messageTime: '11:29',
				messageStatus: '0'
			}],

			socketMsgQueue: [], // 未发送的消息队列
			textMsg: '', // 输入框中的text
			windowHeight: '',
		}
	},
	
	computed: {
		...mapState([
			'ChatMessageCard',
		])
	},
	
	watch: {
		ChatMessageCard(newVal, oldVal) { //监听数据变化，即可做相关操作
			console.log("newVal:");
			console.log(newVal);
			// 渲染到窗口
			newVal.flag = this.chat.FRIEND;
			this.chatContent.push(newVal);
			
			this.scrollToBottom();
			
		}
	},
	
	onLoad: function(opt) {
		uni.setNavigationBarTitle({
			title: "XXXX（聊天人的昵称）"
		});
		// 获取界面传参
		var data = JSON.parse(opt.data);
		userInfo = data.userInfo;
		frindInfo = data.frindInfo;
		
		var that = this;
		uni.getSystemInfo({
		  success: function(res) {
			that.windowHeight = res.windowHeight;
		  }
		});
		
		// 获取与该用户的聊天历史记录
		this.iniChatHistory();
		this.scrollToBottom();
	},
	
	methods: {
		
		sendText() {
			if(!this.textMsg){
				return;
			}
			this.mySocket.sendObj(this.netty.CHAT, frindInfo.id, this.textMsg, null);
			// 渲染到窗口
			var message = {
				messageId: '0006',
				flag: this.chat.ME,
				msg: this.textMsg,
				messageTime: '11:29',
				messageStatus: '1',
			}
			this.chatContent.push(message);
			
			this.textMsg = '';//清空输入框
		},
		
		iniChatHistory(){
			var localChatHistory = this.chat.getUserChatHistory(userInfo.id, frindInfo.id);
			this.chatContent = localChatHistory;
		},
		
		scrollToBottom(){
			// 将页面滚动到底部，延时滚动,等待渲染
			// 页面高度乘以列表长度...绝对能滚到底
			this.$nextTick( function(){
				uni.pageScrollTo({
					scrollTop: this.windowHeight * this.chatContent.length,
					duration: 0
				});
			});
		}
	}
}
